.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
img{
  border: 1px solid rgb(248, 248, 248);
  border-radius:  5px;
  padding: 1px;
  width: 600px;
  height: 80%;
}

.img-produ{
  display: inline;
  position: relative;
  margin: auto;
  height: 0px;
}
.product-image{
  display: inline-flex;
  justify-content: center center;
  align-items: center;
  
}